                  h4.mb-4 Paso 1. Alistamiento para la confeccin de la camisa
                  .row
                    .col-lg-6.mb-4.mb-lg-0
                      p Para el alistamiento de la confeccin de la camisa se necesita tener las piezas cortadas completas:
                      p - Los delanteros
                      p - Las pecheras
                      p - Los puos
                      p - La entretela de la pechera 
                      p - El sesgo de la portauela
                      p - Las mangas con aberturas 
                      p - La portauela
                      p - El cuello y pie de cuello o banda
                      p Adems ser importante contar con lo siguiente:
                      p - El paquete tcnico compuesto por la ficha tcnica, la muestra fsica y el paquete de patrones.
                      p - Todas las herramientas y elementos necesarios, los insumos como son los hilos e hiladillo, botones, talla y marquilla, pie compensando de 1/16 y  para pespuntes, gua imanada, calibrador, agujas, pinzas pulidor y destornillador.
                    .col-lg-6.mb-4.mb-lg-0
