                  h4.mb-4 Paso 2. Preparar cuellos
                  p.mb-4 Unir la entretela fusionable por el lado del pegamento al revs de la tela del cuello con plancha bien caliente, sin vapor, revisando que la tela del cuello y la entretela queden parejas, sin burbujas y sin estirar el tejido.
