                .col-lg-10
                  br
                  h4.mb-4 Paso 3. Cerrar cuello 
                  p.mb-4 Teniendo el cuello por el revs, coser a 1/4 de pulgada con pie universal y gua fija o con pie compensado , en el orillo, cuidando de clavar muy bien la aguja en cada esquina del cuello y que no se corra el material. Rematar al inicio y al final, revisando que quede bien de tensin y pareja la costura.
                  .row
                    .col-lg-6.mb-4.mb-lg-0
                      .titulo-sexto.color-acento-contenido
