              .row.justify-content-center
                .col-lg-10
                  br
                  h4.mb-4 Paso 4. Refilar puntas de cuello
                  p.mb-4 Al terminar de coser el cuello se deben cortar o refilar las puntas de este para que al voltear queden parejas, sin romper el material y se puede vaporizar sacando muy bien las puntas y las costuras.
                  .row
                    .col-lg-6.mb-4.mb-lg-0
                      .titulo-sexto.color-acento-contenido
                        h5 Figura 61
                        p Cortar esquinas de cuello
