              .row.justify-content-center
                .col-lg-10
                  br
                  h4.mb-4 Paso 6. Fusionar pie de cuello y pespuntar
                  p.mb-4 Fusionar la entretela por el lado del pegamento al revs de la tela del pie de cuello con plancha bien caliente sin vapor, revisando que queden parejas, sin burbujas y sin estirar el tejido.
                  .row
                    .col-lg-6.mb-4.mb-lg-0
                      .titulo-sexto.color-acento-contenido
                        h5 Figura 63
                        p Preparacin pie de cuello
                      img(src='@/assets/curso/tema3/img11.png')
