            .tarjeta.color-white
              .row.justify-content-center
                .col-lg-10
                  br
                  h4.mb-4 Paso 7. Doblar borde de pie de cuello y pespuntar
                  p.mb-4 Teniendo el pie de cuello por el revs, voltear la tela 1 cm hacia la entretela fusionada, coser a 1/4 de pulgada con pie universal y gua fija o con pie compensado  en el orillo, respetando la curva y la entretela que limita hasta donde se doble. Rematar al inicio y al final, revisando que quede bien de tensin, pareja la costura y el doblez.
                  .titulo-sexto.color-acento-contenido
                    h5 Figura 64
                    p Doblar y pespuntar pie de cuello
                  img(src='@/assets/curso/tema3/img12.png')
                  br
          .py-3.py-md-4(titulo="8")
            .tarjeta.color-white
              .row.justify-content-center
                .col-lg-10
                  br
                  h4.mb-4 Paso 8. Unir pie de cuello con cuello
                  p.mb-4 Entalegar el cuello con las dos piezas, uniendo los piquetes del centro; se unen cosiendo alrededor de las 3 piezas, uniendo piquetes y los anchos de costura de 1 cm, cuidando de dejar 1 cm para pegarlo luego.
                  p.mb-4 Revisar que las curvas del pie de cuello queden iguales, refilando si es necesario las puntas del cuello.
                  .titulo-sexto.color-acento-contenido
                    h5 Figura 65
