                .col-lg-10
                  br
                  h4.mb-4 Paso 9. Voltear cuello y pespuntar
                  p.mb-4 Voltear el pie de cuello y sacar esquinas sin romper el material, conservando la forma curva y verificando que quede parejo alrededor.
                  p.mb-4 Pespuntar desde la costura del dobladillo a 1/16 por el rededor del cuello y marcar por la parte interna de la pestaa de 1 cm para pegar posteriormente a la camisa.
                  .row
                    .col-lg-10.mb-4.mb-lg-0
                      .titulo-sexto.color-acento-contenido
                        h5 Figura 66
                        p Unir pie de cuello y cuello
                      img(src='@/assets/curso/tema3/img14.png')
