                .col-lg-10
                  br
                  .row.align-items-center
                    .col-lg-6.mb-4.mb-lg-0
                      h4.mb-4 Paso 10. Preparar portauela
                      p.mb-4 De acuerdo con el tipo, el sesgo y la portauela se preforma con plancha a buena temperatura, doblando cada lado segn la forma de este, con cuidado de no estirar el material a 1 cm, cuidando que el largo y el ancho queden iguales.
                      p.mb-4 El sesgo debe quedar con doble dobles y tambin se puede unir con el sesgador de plana sin preformarlo.
                    .col-lg-6.mb-4.mb-lg-0
                      .titulo-sexto.color-acento-contenido
                        h5 Figura 67
                        p Preparacin portauela
                      img(src='@/assets/curso/tema3/img15.png')
                  br
          .py-3.py-md-4(titulo="11")
            .tarjeta.color-white
              .row.justify-content-center
                .col-lg-11
                  br
                  h4.mb-4 Paso 11. Pegar sesgo portauela a abertura interna 
                  p Al final de la abertura de la manga se hacen dos piquetes en diagonal para dar espacio, se procede a pegar el sesgo, colocndolo en medio de la pieza, unindolo a 1/16 y terminando 1cm arriba de la abertura, rematando y cortando el sobrante.
                  p En este final se hace un atraque (remate fino) uniendo sesgo con piquete de manga.
                  p.mb-4 Se procede a unir el otro lado de la portauela, unindolo por el revs sobre el doblez planchado, rematando al inicio y al final. Se voltea al derecho acomodando la portauela.

                  .row
                    .col-lg-8.mb-4.mb-lg-0
                      .titulo-sexto.color-acento-contenido
                        h5 Figura 68
                        p Pegar portauela
                      img(src='@/assets/curso/tema3/img16.png')
                  br
          .py-3.py-md-4(titulo="12")
            .tarjeta.color-white
              .row.justify-content-center
                .col-lg-11
                  br
                  h4.mb-4 Paso 12. Pegar portauela a abertura externa
                  p Se procede a unir el otro lado de la portauela, unindolo por el revs sobre el doblez planchado, rematando al inicio y al final. Se voltea al derecho, acomodando la portauela y se pespunta a 1/16 con pie compensado unindola a la manga, clavar la aguja formando la figura y se llega hasta 1 cm ms debajo de la abertura donde clava aguja y se voltea en forma vertical para hacer el remate.
                  p Se revisa que quede bien los pespuntes, el pico de la portauela y que por debajo quede guardada la costura.
                  .row
                    .col-lg-8.mb-4.mb-lg-0
                      .titulo-sexto.color-acento-contenido
                        h5 Figura 69
                        p Pegar portauela
                      img(src='@/assets/curso/tema3/img17.png')
                  br
          .py-3.py-md-4(titulo="13")
            .tarjeta.color-white
              .row.justify-content-center
                .col-lg-11
                  br
                  h4.mb-4 Paso 13. Preparar puos
                  p Pegar la entretela fusionable por el lado del pegamento al revs de la tela del puo con plancha bien caliente sin vapor, revisando que puo y entretela queden parejas sin burbujas y sin estirar el tejido.
                  .row
                    .col-lg-8.mb-4.mb-lg-0
                      .titulo-sexto.color-acento-contenido
                        h5 Figura 70
                        p Preparacin puo
