            .tarjeta.color-white
              .row.justify-content-center
                .col-lg-11
                  br
                  h4.mb-4 Paso 14. Doblar borde de puo y pespuntar
                  p Teniendo el puo por el revs, voltear la tela 1cm hacia la entretela fusionada, coser a 1/4 de pulgada con pie universal y gua fija o con pie compensado , en el orillo y rematar al inicio y al final, revisando que quede bien de tensin, pareja la costura y el doblez.
                  .row
                    .col-lg-8.mb-4.mb-lg-0
                      .titulo-sexto.color-acento-contenido
                        h5 Figura 71
                        p Doblar y pespuntar puo
                      img(src='@/assets/curso/tema3/img19.png')
                  br
          .py-3.py-md-4(titulo="15")
            .tarjeta.color-white
              .row.justify-content-center
                .col-lg-11
                  br
                  h4.mb-4 Paso 15. Cerrar puo
                  p Cerrar el puo a cada lado, conservando la forma a 1 cm, con pie universal, rematando al inicio y al final, dejando una pestaa de 1cm en el borde para pegarlo a la manga.
                  .row
                    .col-lg-8.mb-4.mb-lg-0
                      .titulo-sexto.color-acento-contenido
                        h5 Figura 72
                        p Cerrar puo
                      img(src='@/assets/curso/tema3/img20.png')
