              .row.justify-content-center
                .col-lg-11
                  br
                  h4.mb-4 Paso 16. Voltear puo y pespuntar
                  .titulo-sexto.color-acento-contenido
                    h5 Figura 73
                    p Voltear puo y pespuntar
                  .row
                    .col-lg-6.mb-4.mb-lg-0
                      .row.justify-content-center
                        .col-lg-6.mb-4.mb-lg-0
                          figure.mb-4
                            img(src='@/assets/curso/tema3/img21.png')
                      p.mb-4 Voltear puo y sacar esquinas sin romper el tejido, conservando la forma y verificando que quede parejo alrededor.
                    .col-lg-6.mb-4.mb-lg-0
                      img(src='@/assets/curso/tema3/img22.png')
                      p.mb-4 Pespuntar desde la costura del dobladillo a 1/16 por el rededor y marcar por la parte interna de la pestaa de 1 cm para pegar posteriormente.
                  br
          .py-3.py-md-4(titulo="17")
            .tarjeta.color-white
              .row.justify-content-center
                .col-lg-11
                  br
                  h4.mb-4 Paso 17. Preparacin de los delanteros
                  p Los patrones de la camisa deben tener sealado con piquetes las distancias para doblar y crear la perilla, estos van de acuerdo con la medida con la cual quedar terminada o segn diseo. Para ello, en el centro de los piquetes ir entretela termofusionada previamente y se plancha formando la perilla por los piquetes, esto para los delanteros derechos. 
                  p Para el delantero izquierdo debe ir marcado con el piquete de costura para aadir la perilla independiente ya preparada con entretela termoadhesiva.
                  .row
                    .col-lg-8.mb-4.mb-lg-0
                      .titulo-sexto.color-acento-contenido
                        h5 Figura 74
                        p Preparacin perilla de camisa
                      img(src='@/assets/curso/tema3/img23.png')
                  br
          .py-3.py-md-4(titulo="18")
            .tarjeta.color-white
              .row.justify-content-center
                .col-lg-11
                  br
                  h4.mb-4 Paso 18. Pespuntar pechera o perilla delantera
                  p Ubicando las piezas derecho con derecho del delantero y de la perilla o pechera, en el orillo de la entretela, coser a 1 cm y rematar al inicio y al final. 
                  p Se voltea y se pespunta con un pie compensado a 1/16 hacia el lado derecho de la costura guardndola, asentando la pechera o perilla al delantero y rematando al inicio y al final, revisando que quede bien, luego realizar otro pespunte a la misma medida en el orillo de esta. 
                  .row
                    .col-lg-6.mb-4.mb-lg-0
                      .cajon.color-primario.p-4.mb-4
                        strong Nota. 
                        | Los dos dobleces son para adentro de la camisa hacia el revs.
                      p Revisar que las costuras queden parejas a un 1/16, que el ancho se conserve de inicio hasta el final, luego planchar.
                    .col-lg-3.mb-4.mb-lg-0
                      .titulo-sexto.color-acento-contenido
                        h5 Figura 75
                        p Perilla de camisa terminada
                      img(src='@/assets/curso/tema3/img24.png')
                  br
          .py-3.py-md-4(titulo="19")
            .tarjeta.color-white
              .row.justify-content-center
                .col-lg-11
                  br
                  h4.mb-4 Paso 19. Preparacin de los bolsillos
                  .row
                    .col-lg-6.mb-4.mb-lg-0
                      p.mb-4 La primera operacin para realizar el pegado del bolsillo es pespuntar la boca de este.
                      p Cada bolsillo segn su estilo debe contar con una plantilla para preformarlo con la plancha antes de ser pegado.
                    .col-lg-5.mb-4.mb-lg-0
                      .titulo-sexto.color-acento-contenido
                        h5 Figura 76
                        p Preparacin bolsillo de camisa
                      img(src='@/assets/curso/tema3/img25.png')
                  br
          .py-3.py-md-4(titulo="20")
            .tarjeta.color-white
              .row.justify-content-center
                .col-lg-11
                  br
                  h4.mb-4 Paso 20. Dobladillar boca de bolsillo
                  .row
                    .col-lg-6.mb-4.mb-lg-0
                      p.mb-4 Teniendo el bolsillo por el revs, voltear la tela 1 cm y luego llevar al segundo piquete del ancho de la boca del bolsillo a 2 cm o segn diseo. Coser con un pie compensado a 1/16 en el orillo, rematar al inicio y al final, revisando que quede bien de tensin, pareja la costura y el doblez.
                      .cajon.color-primario.p-4.mb-4
                        strong Nota. 
                        | Segn diseo se puede realizar otra costura a . 
                      p Otra forma de hacerlo es doblando a los 2 cm y luego nuevamente para coser a  sobre el borde del doblez para crear una pestaa. Rematar al inicio o al final. Esta pestaa tambin se puede asentar a 1/16.
                    .col-lg-5.mb-4.mb-lg-0
                      .titulo-sexto.color-acento-contenido
                        h5 Figura 77
                        p Bolsillo con pespunte
                      img(src='@/assets/curso/tema3/img26.png')
                  br
          .py-3.py-md-4(titulo="21")
            .tarjeta.color-white
              .row.justify-content-center
                .col-lg-11
                  br
                  h4.mb-4 Paso 21. Planchar el bolsillo preformndolo
                  p De acuerdo con el tipo de bolsillo se preforma este con plancha a buena temperatura, doblando cada lado segn la forma, con cuidado de no estirar el material. Las plantillas deben estar a la medida sin costuras para que los dobleces queden iguales, cuidando que el largo y el ancho queden iguales.
                  .row
                    .col-lg-4.mb-4.mb-lg-0
                      .titulo-sexto.color-acento-contenido
                        h5 Figura 78
                        p Bolsillo doblado con plantilla
                      img(src='@/assets/curso/tema3/img27.png')
                  br
          .py-3.py-md-4(titulo="22")
            .tarjeta.color-white
              .row.justify-content-center
                .col-lg-11
                  br
                  h4.mb-4 Paso 22. Pegar o parchar el bolsillo
                  .row
                    .col-lg-6.mb-4.mb-lg-0
                      p.mb-4 Segn los puntos marcados colocar el bolsillo sobre el delantero o donde se vaya a pegar alinendolo, posicionar el bolsillo bajo el prensatelas, clavando aguja una puntada antes y se cose a 1/16 y en cada esquina se clava aguja, se levanta el pie prensatelas para voltear y seguir cosiendo hasta pegarlo completamente, al final rematar. Tambin puede coser a  de medida.
                      p.mb-4 Revisar las puntadas de remate, costuras rectas y esquinas, al revs costuras parejas. 
                    .col-lg-3.mb-4.mb-lg-0
                      .titulo-sexto.color-acento-contenido
                        h5 Figura 79
                        p Bolsillo pegado
                      img(src='@/assets/curso/tema3/img28.png')
                  br
          .py-3.py-md-4(titulo="23")
            .tarjeta.color-white
              .row.justify-content-center
                .col-lg-11
                  br
                  h4.mb-4 Paso 23. Pegar marquilla y talla
                  p Pegar marquilla y talla centrada en la vista de la almilla por el derecho de esta y a 1/16 de medida, rematando al inicio y al final.
                  .row
                    .col-lg-5.mb-4.mb-lg-0
                      .titulo-sexto.color-acento-contenido
                        h5 Figura 80
                        p Pegar marquilla y talla
                      img(src='@/assets/curso/tema3/img29.png')
                  br
          .py-3.py-md-4(titulo="24")
            .tarjeta.color-white
              .row.justify-content-center
                .col-lg-11
                  br
                  h4.mb-4 Paso 24. Unir caja o prense en la espalda
                  .titulo-sexto.color-acento-contenido
                    h5 Figura 81
                    p Unir caja o prense en la espalda
                  .row
                    .col-lg-4.mb-4.mb-lg-0
                      .row
                        .col-lg-10.mb-4.mb-lg-0
                          img(src='@/assets/curso/tema3/img30.png')
                      p Ubicando la espalda por el derecho unir los piquetes, coser a  cm y rematar al inicio y al final.
                    .col-lg-4.mb-4.mb-lg-0
                      .row
                        .col-lg-11.mb-4.mb-lg-0
                          img(src='@/assets/curso/tema3/img31.png')
                      p Los dos dobleces son para adentro de la camisa hacia el revs para formar la tabla y si se quiere prense, para  este se har lo contrario, la tabla ser en el revs de la tela.
                    .col-lg-4.mb-4.mb-lg-0
                      .row
                        .col-lg-9.mb-4.mb-lg-0
                          img(src='@/assets/curso/tema3/img32.png')
                      p Revisar que las costura queden parejas a un 1/16, que el ancho de la tabla se conserve de inicio hasta el final y luego planchar.
                  br
          .py-3.py-md-4(titulo="25")
            .tarjeta.color-white
              .row.justify-content-center
                .col-lg-11
                  br
                  h4.mb-4 Paso 25. Unir Almilla
                  p Posicionar las piezas de la almilla sobre la espalda, encarando las partes rectas de este derecho de vista de almilla con revs de espalda y derecho con derecho de almilla y espalda, uniendo los piquetes centrales, juntando las tres piezas para que queden parejas.
                  p Unir con costura, dejando 1 cm de pestaa, rematando al inicio y al final.
                  .row
                    .col-lg-7.mb-4.mb-lg-0
                      .titulo-sexto.color-acento-contenido
                        h5 Figura 82
                        p Unir Almilla
                      img(src='@/assets/curso/tema3/img33.png')
                  br
          .py-3.py-md-4(titulo="26")
            .tarjeta.color-white
              .row.justify-content-center
                .col-lg-11
                  br
                  h4.mb-4 Paso 26. Pespuntar almilla
                  p Despus de unidas las tres piezas pespuntar la almilla a 1/16 de medida sobre esta, cuidando de que la vista no quede embolsada. 
                  .row
                    .col-lg-5.mb-4.mb-lg-0
                      .titulo-sexto.color-acento-contenido
                        h5 Figura 83
                        p Pespuntar almilla
                      img(src='@/assets/curso/tema3/img34.png')
                  br

      .tarjeta.color-secundario.p-4(titulo="Operaciones de ensamble")
        TabsC.color-primario.mb-5
          .py-3.py-md-4(titulo="1")
            .tarjeta.color-white
              .row.justify-content-center
                .col-lg-10
                  br
                  h4.mb-4 Paso 1. Unir hombros
                  p.mb-4 Unir los hombros, tomando el delantero por el derecho y la almilla de la espalda de la misma forma. Girar la vista de la almilla para que quede por el revs, cubriendo el delantero y la almilla, entalegndolo. Coser a 1 cm por el lado de escote de cuello.
                  p.mb-4 Voltear nuevamente al derecho para que oculte la costura y se realiza la misma operacin con el otro hombro. 
                  .row
                    .col-lg-5.mb-4.mb-lg-0
                      .titulo-sexto.color-acento-contenido
                        h5 Figura 84
                        p Unir hombros
                      img(src='@/assets/curso/tema3/img35.png')
                  br
          .py-3.py-md-4(titulo="2")
            .tarjeta.color-white
              .row.justify-content-center
                .col-lg-10
                  br
                  h4.mb-4 Paso 2. Pespuntar hombros 
                  .row
                    .col-lg-6.mb-4.mb-lg-0
                      p.mb-4 Luego de unir los hombros pespuntar a 1/16 con pie compensado derecho, llevando la costura hacia el posterior de cada hombro, rematando al inicio y al final, cuidando que la vista de la almilla no quede embolsada, ni con arrugas.
                    .col-lg-4.mb-4.mb-lg-0
                      .titulo-sexto.color-acento-contenido
                        h5 Figura 85
                        p Pespuntar hombros
                      img(src='@/assets/curso/tema3/img36.png')
                  br
          .py-3.py-md-4(titulo="3")
            .tarjeta.color-white
              .row.justify-content-center
                .col-lg-10
                  br
                  h4.mb-4 Paso 3. Pegar cuello 
                  .row
                    .col-lg-6.mb-4.mb-lg-0
                      p.mb-4 El cuello debe estar marcado alrededor y sobre el borde de la vista del cuello. Se une derecho con derecho, alineando iguales en la perilla y la punta del cuello y, se cose por la lnea marcada sin formar prenses y uniendo piquetes del centro de cuello. 
                      p.mb-4 Al finalizar las dos piezas deben estar alineadas sin formar prenses, teniendo cuidado en la curva, siguiendo la forma y rematando muy bien.
                    .col-lg-6.mb-4.mb-lg-0
                      .titulo-sexto.color-acento-contenido
                        h5 Figura 86
                        p Pegar cuello
                      img(src='@/assets/curso/tema3/img37.png')
                  br
          .py-3.py-md-4(titulo="4")
            .tarjeta.color-white
              .row.justify-content-center
                .col-lg-10
                  br
                  h4.mb-4 Paso 4. Pespuntar cuello
                  .row
                    .col-lg-6.mb-4.mb-lg-0
                      p.mb-4 Para asentar el cuello se puede utilizar el pie compensado a 1/16, guardando la costura hacia el cuello, pespuntando alrededor del cuello, cuidando que no quede embolsado por el revs y rematando. 
                    .col-lg-6.mb-4.mb-lg-0
                      .titulo-sexto.color-acento-contenido
                        h5 Figura 87
                        p Pespuntar cuello
                      img(src='@/assets/curso/tema3/img38.png')
                  br
          .py-3.py-md-4(titulo="5")
            .tarjeta.color-white
              .row.justify-content-center
                .col-lg-10
                  br
                  h4.mb-4 Paso 5. Pegar mangas
                  .row
                    .col-lg-6.mb-4.mb-lg-0
                      p.mb-4 Para unir las mangas se posicionan derecho con derecho de la tela y la portauela debe coincidir con la espalda de la prenda. Se alinean las telas y se cose en mquina fileteadora con puntada de seguridad, teniendo en cuenta los piquetes del centro de la manga, con cuidado de que no falte ni sobre material, conservando la curva de la manga sin formar prenses. Lo mismo se hace con la otra manga, revisando la calidad de las costuras. 
                    .col-lg-6.mb-4.mb-lg-0
                      .titulo-sexto.color-acento-contenido
                        h5 Figura 88
                        p Pegar mangas
                      img(src='@/assets/curso/tema3/img39.png')
                  br
          .py-3.py-md-4(titulo="6")
            .tarjeta.color-white
              .row.justify-content-center
                .col-lg-10
                  br
                  h4.mb-4 Paso 6. Pespuntar mangas
                  .row
                    .col-lg-6.mb-4.mb-lg-0
                      p.mb-4 Se debe enviar la costura hacia el cuerpo de la blusa y con el pie compensado derecho a 1/16, pespuntar sobre este, respetando la curva de la manga con remate al inicio y al final.
                    .col-lg-6.mb-4.mb-lg-0
                      .titulo-sexto.color-acento-contenido
                        h5 Figura 89
                        p Pespuntar mangas
                      img(src='@/assets/curso/tema3/img40.png')
                  br
          .py-3.py-md-4(titulo="7")
            .tarjeta.color-white
              .row.justify-content-center
                .col-lg-10
                  br
                  h4.mb-4 Paso 7. Cerrar costados
                  .row
                    .col-lg-6.mb-4.mb-lg-0
                      p.mb-4 Posicionando la prenda por el revs de la tela se toma por el delantero, respetando el derecho del filete; de modo que se filetea desde la manga al bajo o ruedo y el otro lado, desde el bajo o ruedo hasta la manga con fileteadora de puntada de seguridado, cuidando de unir los piquetes de la cintura y casando las sisas. 
                    .col-lg-6.mb-4.mb-lg-0
                      .titulo-sexto.color-acento-contenido
                        h5 Figura 90
                        p Cerrar costados
                      img(src='@/assets/curso/tema3/img41.png')
                  br
          .py-3.py-md-4(titulo="8")
            .tarjeta.color-white
              .row.justify-content-center
                .col-lg-10
                  br
                  h4.mb-4 Paso 8. Pegar puos
                  p.mb-4 Unir los prenses de la manga hacia el lado de la portauela. Con el puo preparado y marcado por el borde se une a la manga derecho con derecho, alineando al inicio sin correrlo y se cose sin formar prenses por debajo, llevando las dos telas y juntando al final. 
                  p.mb-4 Lo mismo se hace con el otro puo, rematando al inicio y al final.
                  .row
                    .col-lg-11.mb-4.mb-lg-0
                      .titulo-sexto.color-acento-contenido
                        h5 Figura 91
                        p Pegar puos
                      img(src='@/assets/curso/tema3/img42.png')
                  br
          .py-3.py-md-4(titulo="9")
            .tarjeta.color-white
              .row.justify-content-center
                .col-lg-10
                  br
                  h4.mb-4 Paso 9. Pespuntar puos
                  .row
                    .col-lg-6.mb-4.mb-lg-0
                      p.mb-4 Se inicia empatando con la costura donde se inici el pegado del puo y se hace a 1/16 con pie compensado o gua fija del lado derecho, se cuida que quede parejo y se remata al inicio y al final.
                    .col-lg-6.mb-4.mb-lg-0
                      .titulo-sexto.color-acento-contenido
                        h5 Figura 92
                        p Pespuntar puos
                      img(src='@/assets/curso/tema3/img43.png')
                  br
          .py-3.py-md-4(titulo="10")
            .tarjeta.color-white
              .row.justify-content-center
                .col-lg-10
                  br
                  h4.mb-4 Paso 10. Dobladillar ruedo
                  .row
                    .col-lg-6.mb-4.mb-lg-0
                      p.mb-4 Se utiliza el pie compensado del lado izquierdo y la gua imanada doblando 2 veces 1 cm y se cose a 1/16, respetando la curva y la costura de los costados se enva hacia a la espalda, alineando muy bien las juntas de las pecheras. 
                      p.mb-4 Rematar al inicio y al final y hacer revisin de calidad.
                    .col-lg-6.mb-4.mb-lg-0
                      .titulo-sexto.color-acento-contenido
                        h5 Figura 93
                        p Dobladillar ruedo
                      img(src='@/assets/curso/tema3/img44.png')
                  br

      .tarjeta.color-secundario.p-4(titulo="Operaciones de terminacin ")
        TabsC.color-primario.mb-5
          .py-3.py-md-4(titulo="1")
            .tarjeta.color-white
              .row.justify-content-center
                .col-lg-10
                  br
                  h4.mb-4 Paso 1. Marcar ojales y ojalar
                  .row
                    .col-lg-6.mb-4.mb-lg-0
                      p.mb-4 La marcacin de ojales se realiza con la plantilla para marcarlos y ubica en donde se posiciona la aguja para iniciar el sitio del ojal, en los puos queda en la mitad del ancho del puo 2 cm y a 1 cm de donde est la portauela.
                      p.mb-4 El primer ojal del cuello va en transversal y los dems van en forma vertical. Todo esto se hace en mquina ojaladora programable.
                    .col-lg-6.mb-4.mb-lg-0
                      .titulo-sexto.color-acento-contenido
                        h5 Figura 94
                        p Marcar ojales y ojalar
                      img(src='@/assets/curso/tema3/img45.png')
                  br
          .py-3.py-md-4(titulo="2")
            .tarjeta.color-white
              .row.justify-content-center
                .col-lg-10
                  br
                  h4.mb-4 Paso 2. Marcar botones y botonar
                  .row
                    .col-lg-6.mb-4.mb-lg-0
                      p.mb-4 Marcar los botones con la camisa derecho con derecho, alineando las pecheras y marcando por el centro de los ojales y en los puos de igual forma. En la mquina botonadora se posiciona la pechera y en el centro de esta se pegan los botones.
                    .col-lg-6.mb-4.mb-lg-0
                      .titulo-sexto.color-acento-contenido
                        h5 Figura 95
                        p Marcar botones y botonar
                      img(src='@/assets/curso/tema3/img46.png')
                  br
          .py-3.py-md-4(titulo="3")
            .tarjeta.color-white
              .row.justify-content-center
                .col-lg-10
                  br
                  h4.mb-4 Paso 3. Limpiar, revisar y planchar
                  .row
                    .col-lg-6.mb-4.mb-lg-0
                      p.mb-4 Las ltimas operaciones consisten en pulir, limpiando las hebras, cortndolas de raz, revisando la prenda por el delantero y el posterior.
                      p.mb-4 Posteriormente se pasa al planchado y se finaliza con el empaque de la prenda.
                    .col-lg-6.mb-4.mb-lg-0
                      .titulo-sexto.color-acento-contenido
                        h5 Figura 96
                        p Limpiar, revisar y planchar
                      img(src='@/assets/curso/tema3/img47.png')
                  br
    
