    separador
    #t_3_2.titulo-segundo
      .h4 3.2 Pantaln
    
    p.mb-4 Finalmente, se explican las operaciones de preparacin, ensamble y terminacin necesarias para la elaboracin de esta prenda.

    .tarjeta.color-rose.p-4.mb-4
      .row.justify-content-center
        .col-lg-8.mb-4.mb-lg-0
          .h4 Elaboracin de pantaln 
          p.mb-4 La elaboracin de esta prenda consta de tres operaciones, las cuales se indican a continuacin.
          p.mb-4 Aqu se articulan las operaciones de mockup vistas anteriormente con el proceso de ensamble y terminacin de la prenda. 
        .col-lg-2.mb-4.mb-lg-0
          img(src='@/assets/curso/tema3/img48.png')

    TabsB.color-primario.mb-5
      .tarjeta.color-rose.p-4(titulo="Operaciones de preparacin")
        TabsC.color-primario.mb-5
          .py-3.py-md-4(titulo="1")
            .tarjeta.color-white
              .row.justify-content-center
                .col-lg-10
                  br
                  h4.mb-4 Paso 1. Identificacin y preparacin de piezas
                  .row
                    .col-lg-6.mb-4.mb-lg-0
                      p Para el alistamiento de la confeccin del pantaln se necesita tener las piezas cortadas completas:
                      p - Los delanteros
                      p - Los posteriores
                      p - La entretela
                      p - Los bolsillos
                      p - Las hojas de bolsillos
                      p - La aletilla
                      p - El aletilln
                      p - La pretina
                      p - Los pasadores
                      p - Las vistas y falsos
                      p Adems, se requiere lo siguiente:
                      p - El paquete tcnico compuesto por la ficha tcnica, la muestra fsica, paquete de patrones, todas las herramientas y elementos necesarios, los insumos como son las agujas, los hilos e hiladillo, botones, talla y marquilla, pie compensando de 1/16 y  para pespuntes, gua imanada, calibrador, pinzas pulidor y destornillador.
                    .col-lg-6.mb-4.mb-lg-0
                      .titulo-sexto.color-acento-contenido
                        h5 Figura 97
                        p Identificacin y preparacin de piezas
                      img(src='@/assets/curso/tema3/img49.png')
                  br
          .py-3.py-md-4(titulo="2")
            .tarjeta.color-white
              .row.justify-content-center
                .col-lg-10
                  br
                  h4.mb-4 Paso 2. Bolsillo de ribete
                  .row
                    .col-lg-6.mb-4.mb-lg-0
                      p Para el bolsillo de doble ribete se necesita la hoja de bolsillo, el ribete entretela fusionable y la vista del bolsillo. 
                      p Se plancha la entretela a la hoja del bolsillo centrndola. 
                      p Luego, por los piquetes se plancha la hoja de bolsillo, formando el ancho del ribete, usando la plantilla.
                      p Marcar el largo, centrando la plantilla donde inicia y termina el ribete.
                    .col-lg-6.mb-4.mb-lg-0
                      .titulo-sexto.color-acento-contenido
                        h5 Figura 98
                        p Piezas doble ribete
                      img(src='@/assets/curso/tema3/img50.png')
                  br
          .py-3.py-md-4(titulo="3")
            .tarjeta.color-white
              .row.justify-content-center
                .col-lg-10
                  br
                  h4.mb-4 Paso 3. Coser ribete
                  p Doblar el ribete por el primer piquete  (se puede marcar planchndolo) encarando los derechos de la tela y coser a  cm del borde. 
                  p Luego se debe abrir la hoja de bolsillo y doblar hacia el lado contrario, haciendo la misma costura de  cm. Rematar muy bien al inicio y la final de este. 
                  p Revisar que las costuras queden igual de anchas y que la distancia quede de 1 cm.
                  .row
                    .col-lg-11.mb-4.mb-lg-0
                      .titulo-sexto.color-acento-contenido
                        h5 Figura 99
                        p Coser hoja de bolsillo
                      img(src='@/assets/curso/tema3/img51.png')
                  br
          .py-3.py-md-4(titulo="4")
            .tarjeta.color-white
              .row.justify-content-center
                .col-lg-10
                  br
                  h4.mb-4 Paso 4. Cortar ribete y hoja de bolsillo
                  .row
                    .col-lg-6.mb-4.mb-lg-0
                      p Cortar el ribete por toda la mitad, abriendo las costuras y cuidando de no cortar la hoja de bolsillo, 1 cm antes picar a cada lado hasta el remate para que no vayan a quedar prenses.
                    .col-lg-6.mb-4.mb-lg-0
                      .titulo-sexto.color-acento-contenido
                        h5 Figura 100
                        p Cortar ribete y hoja de bolsillo
                      img(src='@/assets/curso/tema3/img52.png')
                  br
          .py-3.py-md-4(titulo="5")
            .tarjeta.color-white
              .row.justify-content-center
                .col-lg-10
                  br
                  h4.mb-4 Paso 5. Coser extremos de ribete y pespuntar
                  p Se voltea la pieza para acomodarla y dar forma a los dos ribetes se ayuda con la plancha y se rematan los extremos a lado y lado que no tienen costura. Se voltea al derecho y se pespunta alrededor a 1/16. Revisar que las costuras estn bien sin pellizco, bien los remates y sin saltos de puntada.
                  p Pespuntar la boca de bolsillo a 1/16 en su parte inferior y extremos, luego cerrar la hoja de bolsillo alineando y por el derecho acabar de hacer el pespunte de 1/16 para sujetarla.
                  .row
                    .col-lg-11.mb-4.mb-lg-0
                      .titulo-sexto.color-acento-contenido
                        h5 Figura 101
                        p Coser extremos de ribetes y pespuntar
                      img(src='@/assets/curso/tema3/img53.png')
                  br
          .py-3.py-md-4(titulo="6")
            .tarjeta.color-white
              .row.justify-content-center
                .col-lg-10
                  br
                  h4.mb-4 Paso 6. Coser extremos de ribete y pespuntar
                  .row
                    .col-lg-6.mb-4.mb-lg-0
                      p Cerrar el bolsillo por el revs a los tres lados en mquina plana si es forrado o en mquina fileteadora. 
                      p Rematar y revisar costuras alrededor.
                    .col-lg-6.mb-4.mb-lg-0
                      .titulo-sexto.color-acento-contenido
                        h5 Figura 102
                        p Cerrar hoja de bolsillo
                      img(src='@/assets/curso/tema3/img54.png')
                  br
          .py-3.py-md-4(titulo="7")
            .tarjeta.color-white
              .row.justify-content-center
                .col-lg-10
                  br
                  h4.mb-4 Paso 7. Filetear falsos y vistas de bolsillo delantero
                  .row
                    .col-lg-6.mb-4.mb-lg-0
                      p Filetear por el derecho el falso y la vista del bolsillo con hilaza e hilo el mismo color de la tela.
                    .col-lg-4.mb-4.mb-lg-0
                      .titulo-sexto.color-acento-contenido
                        h5 Figura 103
                        p Filetear falsos y vistas
                      img(src='@/assets/curso/tema3/img55.png')
                  br
          .py-3.py-md-4(titulo="8")
            .tarjeta.color-white
              .row.justify-content-center
                .col-lg-10
                  br
                  h4.mb-4 Paso 8. Unir falsos y vistas a hoja de bolsillo delantera
                  p Colocar el falso sobre el fondo de bolsillo, el cual tiene el corte de boca de bolsillo. Este se une cosiendo a 1/4 por la costura de filete y alrededor de la hoja de bolsillo. 
                  p Al otro lado del bolsillo ubicar la vista y coserla del mismo modo alrededor.
                  .row
                    .col-lg-7.mb-4.mb-lg-0
                      .titulo-sexto.color-acento-contenido
                        h5 Figura 104
                        p Unir falsos y vistas
                      img(src='@/assets/curso/tema3/img56.png')
                  br
          .py-3.py-md-4(titulo="9")
            .tarjeta.color-white
              .row.justify-content-center
                .col-lg-10
                  br
                  h4.mb-4 Paso 9. Unir hoja de bolsillo a delantero
                  .row
                    .col-lg-6.mb-4.mb-lg-0
                      p Unir al delantero con los fondos de bolsillo, ubicando derecho con derecho el fondo de bolsillo que tiene el corte de boca de bolsillo, con la pernera (delantero) y cosiendo las tres piezas (pernera, falso y fondo de bolsillo) con puntada recta a 1 cm, luego se voltea al revs, sacando muy bien la costura y pespuntando de nuevo a . Cerrar la hoja de bolsillo en caso de que sean piezas independientes. 
                      p Posicionar las piezas del bolsillo con el posterior sin que el bolsillo quede con bolsa o recogido y fijar en la cintura y en parte del costado a  de medida.  La misma operacin se realiza en el otro delantero.
                    .col-lg-6.mb-4.mb-lg-0
                      .titulo-sexto.color-acento-contenido
                        h5 Figura 105
                        p Unir hoja de bolsillo a delantero
                      img(src='@/assets/curso/tema3/img57.png')
                  br
          .py-3.py-md-4(titulo="10")
            .tarjeta.color-white
              .row.justify-content-center
                .col-lg-10
                  br
                  h4.mb-4 Paso 10. Identificacin y preparacin de piezas para pegar cierre de pantaln
                  .row
                    .col-lg-6.mb-4.mb-lg-0
                      p Para el alistamiento de la confeccin del cierre del pantaln se necesita tener las piezas dispuestas para ello, como son:
                      p - Delanteros
                      p - Aletilla y aletilln
                      p Adems de todas las herramientas y elementos necesarios, los insumos como son los hilos y el zipper o cremallera.
                    .col-lg-6.mb-4.mb-lg-0
                      .titulo-sexto.color-acento-contenido
                        h5 Figura 106
                        p Preparacin piezas
                      img(src='@/assets/curso/tema3/img58.png')
                  br
          .py-3.py-md-4(titulo="11")
            .tarjeta.color-white
              .row.justify-content-center
                .col-lg-10
                  br
                  h4.mb-4 Paso 11. Preparacin de piezas 
                  .row
                    .col-lg-6.mb-4.mb-lg-0
                      p Filetear cada una de las piezas del delantero por el tiro y la aletilla; el aletilln se cierra en mquina plana en la parte inferior entalegndolo y, filetear luego por el borde de este.
                    .col-lg-6.mb-4.mb-lg-0
                      .titulo-sexto.color-acento-contenido
                        h5 Figura 107
                        p Filetear piezas
                      img(src='@/assets/curso/tema3/img59.png')
                  br
          .py-3.py-md-4(titulo="12")
            .tarjeta.color-white
              .row.justify-content-center
                .col-lg-10
                  br
                  h4.mb-4 Paso 12. Pegar aletilla a delantero izquierdo
                  .row
                    .col-lg-6.mb-4.mb-lg-0
                      p Por el derecho del delantero izquierdo unir la aletilla tambin por el derecho encarando, coser a 1 cm hasta llegar al piquete. 
                      p Rematar al inicio y al final.
                    .col-lg-4.mb-4.mb-lg-0
                      .titulo-sexto.color-acento-contenido
                        h5 Figura 108
                        p Unir aletilla a delantero
                      img(src='@/assets/curso/tema3/img60.png')
                  br
          .py-3.py-md-4(titulo="13")
            .tarjeta.color-white
              .row.justify-content-center
                .col-lg-10
                  br
                  h4.mb-4 Paso 13. Pespuntar Aletilla
                  .row
                    .col-lg-6.mb-4.mb-lg-0
                      p Voltear la aletilla hacia el revs del delantero, sacando muy bien la costura para que no queden pliegues, pespuntar a 1/16 de medida y luego a  de medida hasta donde termina la aletilla; el tiro del pantaln debe quedar con un doblez de 1 cm para cerrarlo posteriormente. 
                      p Rematar al inicio y al final de cada pespunte.
                    .col-lg-4.mb-4.mb-lg-0
                      .titulo-sexto.color-acento-contenido
                        h5 Figura 109
                        p Pespuntar aletilla
                      img(src='@/assets/curso/tema3/img61.png')
                  br
          .py-3.py-md-4(titulo="14")
            .tarjeta.color-white
              .row.justify-content-center
                .col-lg-10
                  br
                  h4.mb-4 Paso 14. Pegar cremallera
                  p Unir el cierre derecho con derecho a la aletilla, al final deben coincidir y levantar la pestaa del cierre que va al borde. 
                  p Del tope de la cremallera hacia abajo mide 1 cm y se dibuja una curva llamada J. Se comienza a coser por la curva uniendo este por todo el borde rematando, luego hacer otra costura al 1/4 de medida o al ancho del pie tambin rematando al inicio y al final.
                  .row
                    .col-lg-6.mb-4.mb-lg-0
                      .cajon.color-acento-contenido.p-4.mb-4
                        strong Nota. 
                        | Estas operaciones de doble costura se pueden hacer con mquina de dos agujas. 
                  .row
                    .col-lg-8.mb-4.mb-lg-0
                      .titulo-sexto.color-acento-contenido
                        h5 Figura 110
                        p Pegar cremallera a aletilla
                      img(src='@/assets/curso/tema3/img62.png')
                  br
          .py-3.py-md-4(titulo="15")
            .tarjeta.color-white
              .row.justify-content-center
                .col-lg-10
                  br
                  h4.mb-4 Paso 15. Pegar cremallera a delantero con aletilln
                  .row
                    .col-lg-6.mb-4.mb-lg-0
                      p Tomar el delantero izquierdo y doblar la aletilla y el cierre hacia afuera, luego posicionar el delantero derecho (1) encima el delantero izquierdo (2) al borde de la cintura y del tiro y por ltimo, el aletilln, se cose a 1/4 de medida o al ancho de pie universal, uniendo las tres piezas, cuidando de no coser la aletilla del delantero izquierdo. 
                    .col-lg-6.mb-4.mb-lg-0
                      .titulo-sexto.color-acento-contenido
                        h5 Figura 111
                        p Pegar cremallera a delantero con aletilln
                      img(src='@/assets/curso/tema3/img63.png')
                  br
          .py-3.py-md-4(titulo="16")
            .tarjeta.color-white
              .row.justify-content-center
                .col-lg-10
                  br
                  h4.mb-4 Paso 16. Pespuntar delantero derecho por el derecho
                  .row
                    .col-lg-6.mb-4.mb-lg-0
                      p Se pespunta el delantero derecho a 1/16 de medida con pie compensado para fijar este al cierre por el derecho de la tela. 
                      p Rematar al inicio y al final y, cuidar que no se tome ms de la medida del dobles del tiro.
                    .col-lg-5.mb-4.mb-lg-0
                      .titulo-sexto.color-acento-contenido
                        h5 Figura 112
                        p Pespuntar delantero derecho por el derecho
                      img(src='@/assets/curso/tema3/img64.png')
                  br
          .py-3.py-md-4(titulo="17")
            .tarjeta.color-white
              .row.justify-content-center
                .col-lg-10
                  br
                  h4.mb-4 Paso 17. Unir tiros 
                  .row
                    .col-lg-6.mb-4.mb-lg-0
                      p Por el revs de la prenda y juntando los dos tiros delanteros se procede a unirlos a 1 cm de costura, cuidando que no queden arrugas y desde donde permita hacer la costura.
                    .col-lg-5.mb-4.mb-lg-0
                      .titulo-sexto.color-acento-contenido
                        h5 Figura 113
                        p Unir tiros
                      img(src='@/assets/curso/tema3/img65.png')
                  br
          .py-3.py-md-4(titulo="18")
            .tarjeta.color-white
              .row.justify-content-center
                .col-lg-10
                  br
                  h4.mb-4 Paso 18. Pespuntar tiro 
                  .row
                    .col-lg-6.mb-4.mb-lg-0
                      p Por el derecho de la prenda y con pie compensado a 1/16 y luego a 1/4 se procede a pespuntar desde el tiro, doblando la costura interna hacia el lado izquierdo hasta el final de la J del tiro, conservando la forma y el ancho de pespunte.
                      p Unir por el revs la aletilla y el aletilln con un remate o presilla para que no quede suelto. 
                    .col-lg-6.mb-4.mb-lg-0
                      .titulo-sexto.color-acento-contenido
                        h5 Figura 114
                        p Pespuntar tiro
                      img(src='@/assets/curso/tema3/img66.png')
                  br
          .py-3.py-md-4(titulo="19")
            .tarjeta.color-white
              .row.justify-content-center
                .col-lg-10
                  br
                  h4.mb-4 Paso 19. Identificacin y preparacin de piezas de la pretina
                  .row
                    .col-lg-6.mb-4.mb-lg-0
                      p La pretina recta es un rectngulo de tela del cual tiene la medida del contorno delantero y del posterior, ms lo que tiene la aletilla y el monte (6 cm) por el ancho, ms la costura. 
                      p Esta debe ir con entretela fusionable solo con la mitad del ancho de la pretina. 
                    .col-lg-6.mb-4.mb-lg-0
                      .titulo-sexto.color-acento-contenido
                        h5 Figura 115
                        p Piezas pretina
                      img(src='@/assets/curso/tema3/img67.png')
                  br
          .py-3.py-md-4(titulo="20")
            .tarjeta.color-white
              .row.justify-content-center
                .col-lg-10
                  br
                  h4.mb-4 Paso 20. Preformar pretina
                  .row
                    .col-lg-6.mb-4.mb-lg-0
                      p Luego de pegar la entretela 1 cm del borde, doblar esa pestaa hacia el revs, planchando muy bien.
                    .col-lg-6.mb-4.mb-lg-0
                      .titulo-sexto.color-acento-contenido
                        h5 Figura 116
                        p Preformar pretina
                      img(src='@/assets/curso/tema3/img68.png')
                  br
          .py-3.py-md-4(titulo="21")
            .tarjeta.color-white
              .row.justify-content-center
                .col-lg-10
                  br
                  h4.mb-4 Paso 21. Cerrar costados de pretina 
                  .row
                    .col-lg-6.mb-4.mb-lg-0
                      p Luego de planchada se voltea y derecho con derecho cerrar a 1 cm por los extremos. 
                      p Cuidar de que quede parejo y rematar muy bien.
                    .col-lg-6.mb-4.mb-lg-0
                      .titulo-sexto.color-acento-contenido
                        h5 Figura 117
                        p Cerrar costados de pretina
                      img(src='@/assets/curso/tema3/img69.png')
                  br
          .py-3.py-md-4(titulo="22")
            .tarjeta.color-white
              .row.justify-content-center
                .col-lg-10
                  br
                  h4.mb-4 Paso 22. Marcar pretina 
                  .row
                    .col-lg-6.mb-4.mb-lg-0
                      p Voltear la pretina al derecho, sacando muy bien las esquinas sin romper el material y preformar de nuevo con plancha. 
                      p Marcar la medida del lado del borde a la pestaa inferior bien al ras, para dar la medida exacta donde se va a coser y que no se genere entorche o bombas al momento de coser.
                    .col-lg-6.mb-4.mb-lg-0
                      .titulo-sexto.color-acento-contenido
                        h5 Figura 118
                        p Marcar pretina
                      img(src='@/assets/curso/tema3/img70.png')
                  br

      .tarjeta.color-rose.p-4(titulo="Operaciones de ensamble")
        TabsC.color-primario.mb-5
          .py-3.py-md-4(titulo="1")
            .tarjeta.color-white
              .row.justify-content-center
                .col-lg-10
                  br
                  h4.mb-4 Paso 1. Cerrar tiros posteriores
                  .row
                    .col-lg-6.mb-4.mb-lg-0
                      p Una vez hechos los bolsillos posteriores se procede a unir los tiros en la fileteadora con puntada de seguridad, derecho con derecho las perneras posteriores. 
                      p Luego, pespuntar con costura a 1/16 y despus a  o segn diseo.
                    .col-lg-5.mb-4.mb-lg-0
                      .titulo-sexto.color-acento-contenido
                        h5 Figura 119
                        p Cerrar tiros posteriores
                      img(src='@/assets/curso/tema3/img71.png')
                  br
          .py-3.py-md-4(titulo="2")
            .tarjeta.color-white
              .row.justify-content-center
                .col-lg-10
                  br
                  h4.mb-4 Paso 2. Cerrar costados
                  .row
                    .col-lg-6.mb-4.mb-lg-0
                      p Estando ya listos los bolsillos delanteros y posteriores y ya con tiros cerrados se encaran los delanteros y los posteriores por el derecho, uniendo muy bien y se filetea por el lado del delantero para respetar el derecho del filete, se inicia por la bota y en el otro lado por la cintura y se unen en la fileteadora con puntada de seguridad.
                    .col-lg-6.mb-4.mb-lg-0
                      .titulo-sexto.color-acento-contenido
                        h5 Figura 120
                        p Cerrar costados
                      img(src='@/assets/curso/tema3/img72.png')
                  br
          .py-3.py-md-4(titulo="3")
            .tarjeta.color-white
              .row.justify-content-center
                .col-lg-10
                  br
                  h4.mb-4 Paso 3. Cerrar entrepierna
                  .row
                    .col-lg-6.mb-4.mb-lg-0
                      p Unir por el lado de delantero y con la piezas al revs el posterior y el delantero por la entrepierna, haciendo coincidir las dos uniones del tiro, respetando la curva en mquina fileteadora con puntada de seguridad.
                    .col-lg-4.mb-4.mb-lg-0
                      .titulo-sexto.color-acento-contenido
                        h5 Figura 121
                        p Cerrar entrepierna
                      img(src='@/assets/curso/tema3/img73.png')
                  br
          .py-3.py-md-4(titulo="4")
            .tarjeta.color-white
              .row.justify-content-center
                .col-lg-10
                  br
                  h4.mb-4 Paso 4. Unir pretina a pantaln
                  .row
                    .col-lg-6.mb-4.mb-lg-0
                      p Se toma el pantaln por el revs, y se posiciona la pretina por el lado de la pestaa que se marc, tomando el derecho de la pretina con el revs del pantaln se inicia empatando extremos con alfileres y se cose por la marca de la pretina, ajustando bien los bordes de las dos telas.
                    .col-lg-4.mb-4.mb-lg-0
                      .titulo-sexto.color-acento-contenido
                        h5 Figura 122
                        p Unir pretina a pantaln
                      img(src='@/assets/curso/tema3/img74.png')
                  br
          .py-3.py-md-4(titulo="5")
            .tarjeta.color-white
              .row.justify-content-center
                .col-lg-10
                  br
                  h4.mb-4 Paso 5. Asentar pretina
                  .row
                    .col-lg-6.mb-4.mb-lg-0
                      p Luego de unir la pretina, cuadrar las cabezas a lado y lado del pantaln y asentar con costura a 1/16 por todo el contorno, cuidando de no estirar el material.
                    .col-lg-5.mb-4.mb-lg-0
                      .titulo-sexto.color-acento-contenido
                        h5 Figura 123
                        p Asentar pretina
                      img(src='@/assets/curso/tema3/img75.png')
                  br
          .py-3.py-md-4(titulo="6")
            .tarjeta.color-white
              .row.justify-content-center
                .col-lg-10
                  br
                  h4.mb-4 Paso 6. Hacer ruedos a botas
                  .row
                    .col-lg-6.mb-4.mb-lg-0
                      p Como ltima operacin de ensamble se realiza el ruedo con puntada recta en mquina plana, haciendo doble doblez de 1 cm con costura a 1/16 y pie compensado del lado izquierdo y la gua imanada, cuidando que las costuras de los costados y la entrepierna se volteen para el posterior. 
                      p Rematar al inicio y la final de la costura.
                    .col-lg-4.mb-4.mb-lg-0
                      .titulo-sexto.color-acento-contenido
                        h5 Figura 124
                        p Hacer ruedos a botas
                      img(src='@/assets/curso/tema3/img76.png')
                  br

      .tarjeta.color-rose.p-4(titulo="Operaciones de terminacin ")
        TabsC.color-primario.mb-5
          .py-3.py-md-4(titulo="1")
            .tarjeta.color-white
