                .col-lg-10
                  br
                  h4.mb-4 Paso 1. Ojalar y botonar pretina
                  .row
                    .col-lg-6.mb-4.mb-lg-0
                      p Marcar el ojal en la pretina izquierda de forma horizontal con un punto al inicio de este, luego marcar en la mitad del ojal donde ir el botn y pegarlo.
                    .col-lg-5.mb-4.mb-lg-0
                      .titulo-sexto.color-acento-contenido
                        h5 Figura 125
                        p Ojalar y botonar pretina
                      img(src='@/assets/curso/tema3/img77.png')
