          .py-3.py-md-4(titulo="2")
            .tarjeta.color-white
              .row.justify-content-center
                .col-lg-10
                  br
                  h4.mb-4 Paso 2. Limpiar, revisar y planchar
                  .row
                    .col-lg-6.mb-4.mb-lg-0
                      p Las ltimas operaciones consisten en pulir limpiando las hebras, cortndolas de raz, revisando la prenda, por el delantero y el posterior y se pasa a planchado para el posterior empaque.
                    .col-lg-5.mb-4.mb-lg-0
                      .titulo-sexto.color-acento-contenido
                        h5 Figura 126
                        p Limpiar, revisar y planchar
                      img(src='@/assets/curso/tema3/img78.png')
